import styled from 'styled-components';

export const View = styled.div`
    
    width: 100%;
    height: 100px;
    background: #F9F3F3;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
`;

export const DivWhats = styled.div`

    display: flex;
    align-items: center;
    flex-direction: row;

`;

export const TelefoneText = styled.a`
    font-family: 'M PLUS 1p', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    padding: 10px;
`;

export const StyleIcons = styled.div`
    width: 23%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
    margin: 10px;

`

export const Container = styled.div`
    width: 100%;

`;

export const StyleMenu = styled.div`
    width: 100%;
    height: 55px;
    display:flex;
    justify-content:center;
    
    
`;




